/* Дополнительные товары */

.extra {
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 36px;
  margin-top: 28px;
}

.extra__wrapper {
  display: flex;
  flex-wrap: wrap;
}

.extra__list {
  margin: 0;
  list-style: none;
  padding: 0;
  padding-bottom: 39px;
  width: 100%;
}

.extra__item {
  border-top: 1px solid $layer-1;

  &:last-child {
    border-bottom: 1px solid $layer-1;
  }
}

.extra__item-title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
  text-align: left;
  text-transform: uppercase;
  color: $black-three;
  margin: 0;
  padding-top: 13px;
  padding-bottom: 13px;
}

.extra__item-texts {
  display: flex;
  justify-content: space-between;
  padding-bottom: 0;
}

.extra__item-text {
  font-family: "Arial", "Tahoma", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 14px;
  text-align: left;
  color:  $input-act;
}

.extra__gift {
  list-style: none;
  padding: 0;
  margin: 0;
  background-image: url("../img/gift-photo-mobile@1x.jpg");
  width: 280px;
  height: 289px;
}

.extra__item-gift-pic {
  padding-top: 65px;
  padding-left: 113px;
  padding-bottom: 59px;
  fill: $bg-white;
  width: 56px;
  height: 61px;
}

.extra__item-text-gift {
  width: 161px;
  font-family: "Arial", "Tahoma", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-align: center;
  color: $bg-white;
  padding-left: 59px;
}

@media screen and (min-width: $tablet-width) {

  .extra {
    padding: 0;
    margin-top: 62px;
  }

  .extra__wrapper {
    margin-bottom: 76px;
  }

  .extra__list {
    margin-bottom: 69px;
    padding-bottom: 0;
  }

  .extra__item {
    padding-left: 37px;
    padding-right: 37px;
    margin-top: -1px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    border: 1px solid $layer-1;
    border-collapse: collapse;
  }

  .extra__item-title {
    padding-top: 25px;
    width: 352px;
    font-size: 20px;
    line-height: 20px;
  }

  .extra__item-texts {
    padding-top: 29px;
    padding-bottom: 7px;
  }

  .extra__item-text {
    padding-left: 52px;
    font-size: 16px;
    line-height: 20px;
  }

  .extra__gift {
    background-image: url("../img/gift-photo-tablet@1x.jpg");
    width: 708px;
    height: 200px;
    margin: 0 auto;
  }

  .extra__item-gift {
    display: flex;
    justify-content: space-between;
  }

  .extra__item-gift-pic {
    padding-left: 91px;
  }

  .extra__item-text-gift {
    width: 161px;
    text-align: left;
    padding-left: 0;
    margin: 0;
    padding-top: 72px;
    padding-right: 123px;
  }
}

@media screen and (min-width: $desktop-width) {

  .extra {
    margin-top: 81px;
    padding-left: 40px;
  }

  .extra__wrapper {
    margin-bottom: 0;
  }

  .extra__wrapper {
    justify-content: space-between;
  }

  .extra__list {
    width: 895px;
    margin-bottom: 84px;
  }

  .extra__item {
    padding: 0;
    border: none;
    border-top: 1px solid $layer-1;


    &:last-child {
      border-bottom: 1px solid $layer-1;
    }
  }

  .extra__item-title {
    padding-top: 25px;
    padding-right: 54px;
    width: 180px;
  }

  .extra__item-texts {
    padding-top: 29px;
    padding-bottom: 23px;
    width: 334px;
  }

  .extra__item-text {
    padding-left: 52px;
  }

  .extra__gift {
    background: url("../img/gift-photo-desktop@1x.jpg") no-repeat;
    width: 245px;
    height: 289px;
    margin-left: 79px;
  }

  .extra__item-gift {
    flex-wrap: wrap;
  }

  .extra__item-gift-pic {
    padding-top: 63px;
    padding-left: 96px;
    padding-bottom: 61px;
  }

  .extra__item-text-gift {
    padding-top: 0;
    padding-right: 0;
    padding-left: 43px;
    text-align: center;
  }
}
