/* oswald-400 */
@font-face {
  font-family: "Oswald", Arial;
  font-style: normal;
  font-weight: 400;
  src:
    local("oswaldregular"), url("../../fonts/oswaldregular.woff2") format("woff2"),
    url("../../fonts/oswaldregular.woff") format("woff");
}

/* oswald-600 */
@font-face {
  font-family: "Oswald", Arial;
  font-style: normal;
  font-weight: 500;
  src:
    local("oswaldmedium"), url("../../fonts/oswaldmedium.woff2") format("woff2"),
    url("../../fonts/oswaldmedium.woff") format("woff");
}
