/*Коты*/

.slider {
  position: relative;
  display: flex;
  align-items: flex-end;
  height: 326px;
  padding-bottom: 34px;
  padding-left: 20px;
}

.slider__item--slim {
  display: none;
}

.slider__box {
  position: absolute;
  width: 319px;
  top: 35px;
  left: 0;

}

@media screen and (min-width: $tablet-width) {
  .slider {
    height: 462px;
    padding-bottom: 0;
    padding-left: 0;
  }

  .slider__box {
    position: absolute;
    top: -169px;
    left: -15px;
    margin: 0 auto;
    width: 724px;
    display: flex;
  }

  .slider__item {
    display: block;
    position: relative;
    width: 50%;
    height: 517px;
    overflow: hidden;
  }

  .slider__item--pro picture {
    width: 100%;
    height: 100%;
    display: block;
    position: absolute;
    top: 0;
    right: -26px;
  }

  .slider__item--slim picture {
    width: 100%;
    height: 100%;
    display: block;
    position: absolute;
    top: 0;
    left: -334px;
  }

  .slider__item-before-mobile {
    max-width: 683px;
    height: 517px;
  }

  .slider__item-after-mobile {
    max-width: 683px;
    height: 517px;
  }
}

@media screen and (min-width: $desktop-width) {

  .slider__box {
    top: -191px;
    left: -15px;
  }

  .slider__item {
    display: block;
    position: relative;
    width: 50%;
    height: 517px;
    overflow: hidden;
  }

  .slider__item--pro picture {
    top: 0;
    right: -1px;
  }

  .slider__item--slim picture {
    display: block;
    position: absolute;
    top: 0;
    left: -360px;
  }
}
